<template>
<div class="zh-full-screen" id="edit">
  <md-editer :mdText="mdText" @save="saveHandle" @publich="publichHandle"></md-editer>
  <el-dialog :show-close="false" :visible.sync="dialogShow" size="tiny" :lock-scroll="true">
    <div slot="title" class="zh-login-header">
      <h1> opus </h1>
    </div>
    <div class="zh-row">
      <el-input v-model="newOpus.opusTitle" placeholder=""></el-input>
      <p class="zh-tip" v-show="opusTitleTip"></p>
    </div>
    <div class="zh-row">
      <textarea class="zh-textarea" v-model="newOpus.opusSummary" placeholder=""></textarea>
      <p class="zh-tip" v-show="opusSummaryTip"></p>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button size="large" @click="handleCancel"></el-button>
      <el-button type="primary" size="large" @click="handleEnsure"></el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import {
  mapGetters,
  mapMutations,
  mapActions
} from 'vuex'
import mdEditer from 'client/views/model/mdEditer'
export default {
  name: 'editView',
  data() {
    return {
      opusId: '',
      mdText: '',
      dialogShow: false,
      newOpus: {
        opusTitle: '',
        opusSummary: '',
        opusText: '',
      },
      opusTitleTip: false,
      opusSummaryTip: false
    }
  },
  computed: {
    ...mapGetters([
      'editModelCreate',
    ])
  },
  methods: {
    ...mapMutations([
      'setEditModel',
    ]),
    ...mapActions([
      'createOpus',
    ]),
    // 
    saveHandle() {
      this.$message({
        message: '!',
        type: 'success'
      })
    },
    // 
    publichHandle(mdContent) {
      this.newOpus.opusText = mdContent
      if (this.editModelCreate) {
        this.dialogShow = true
        //  opus 
        // ...
        // opus
        // 
      } else {
        console.log('opus')
        // opus
        // 
      }
    },
    handleCancel() {
      this.dialogShow = false
    },
    handleEnsure() {
      // 
      if (!this.newOpus.opusTitle) {
        this.opusTitleTip = true
        return
      }
      if (!this.newOpus.opusSummary) {
        this.opusSummaryTip = true
        return
      }
      this.dialogShow = false
      // 
      this.createOpus(this.newOpus)
    },
  },
  components: {
    mdEditer,
  },
  created() {
    this.opusId = this.$route.params.opusId
    if (this.opusId) {
      this.setEditModel({
        create: false
      })
    } else {
      this.setEditModel({
        create: true
      })
    }
  },
}
</script>

<style lang='stylus'>
</style>



