<template>
<div class="zh-full-screen">
  <!--  -->
  <div class="md-setting">
    <div class="md-setting-item">
      <router-link :to="{ name: 'home'}" class="md-setting-logo">kizun</router-link>
    </div>
    <div class="md-setting-item" @click="lineWrapHandle"></div>
    <!-- <div class="md-setting-item"></div> -->
    <div class="md-setting-item" @click="openFileHandle"></div>
    <input ref="openFile" type="file" style="display:none;" @change="fileChangeHandle">
    <div class="md-setting-item" @click="fullScreenHandle"></div>
    <div class="md-setting-item" @click="publichHandle"></div>
  </div>
  <!--  -->
  <div class="zh-page-main">
    <div class="zh-page-half">
      <textarea ref="CodeMirror">some text ......</textarea>
    </div>
    <div class="zh-page-half">
      <div class="markdown-body" v-html="htmlContent"></div>
    </div>
  </div>
</div>
</template>

<script>
import * as CodeMirror from 'codemirror/lib/codemirror.js'
import * as markdown from 'codemirror/mode/markdown/markdown.js' // model  markdown
import * as closebrackets from 'codemirror/addon/edit/closebrackets.js' // 
import * as continuelist from 'codemirror/addon/edit/continuelist.js' // 
import * as activeLine from 'codemirror/addon/selection/active-line.js' //
import * as placeholder from 'codemirror/addon/display/placeholder.js' // placehoder

import * as marked from 'marked/lib/marked.js'

export default {
  name: "mdEditer",
  props: {
    mdText: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      editor: null, // 
      localMdContent: localStorage.getItem('mdContent'), // md 
      htmlContent: null, // html
    }
  },
  computed: {},
  methods: {
    // 
    lineWrapHandle() {
      this.editor.setOption('lineWrapping', this.editor.getOption('lineWrapping') ? false : true)
    },
    // 
    openFileHandle() {
      let openFile = this.$refs.openFile
      if (document.all) {
        openFile.click()
      } else {
        let e = document.createEvent("MouseEvents")
        e.initEvent("click", true, true)
        openFile.dispatchEvent(e)
      }
    },
    // 
    fileChangeHandle() {
      let app = this
      let openFile = this.$refs.openFile
      let fileData = openFile.files[0]
      if (!fileData || !fileData.type || !fileData.type.match("text.*")) {
        console.error('some thing wrong!')
        return
      }
      let reader = new FileReader()
      reader.onerror = () => {
        console.error("Cannot read file, some eroor occuerd.")
        return
      };
      reader.onload = (evt) => {
        app.editor.setValue(evt.target.result)
      };
      reader.readAsText(fileData, "utf-8")
    },
    // 
    fullScreenHandle() {
      if (!this.editor.getOption('fullScreen')) {
        this.editor.setOption('fullScreen', true)
        // 
        // ...
        // 
        const docElm = document.documentElement
        if (docElm.requestFullscreen) {
          docElm.requestFullscreen();
        } else if (docElm.mozRequestFullScreen) {
          //FireFox
          docElm.mozRequestFullScreen();
        } else if (docElm.webkitRequestFullScreen) {
          //Chrome
          docElm.webkitRequestFullScreen();
        } else if (elem.msRequestFullscreen) {
          //IE11
          elem.msRequestFullscreen();
        }
      } else {
        this.editor.setOption('fullScreen', false)
        // 
        // ...
        // 
        if (document.exitFullscreen) {
          document.exitFullscreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
      }
    },
    // 
    publichHandle() {
      this.$emit('publich', this.editor.getValue())
    },
    // markdown
    saveToLocal() {
      localStorage.setItem('mdContent', this.editor.getValue())
      this.$emit('save')
    }
  },
  mounted() {
    // 
    this.editor = CodeMirror.fromTextArea(this.$refs.CodeMirror, {
      mode: "markdown", // 
      theme: 'default', // 
      lineWrapping: true, // 
      lineNumbers: true, // 
      autofocus: true, // 
      fullScreen: false, // 
      placeholder: 'Code goes here...', //
      autoCloseBrackets: true, // 
      styleActiveLine: true, //
      extraKeys: {
        "Enter": "newlineAndIndentContinueMarkdownList", //markdown
      }
    })
    // 
    if (this.mdText) {
      this.editor.setValue(this.mdText)
      this.htmlContent = marked(this.mdText)
    } else if (this.localMdContent) {
      this.editor.setValue(this.localMdContent)
      this.htmlContent = marked(this.localMdContent)
    }
    let app = this
    this.editor.setOption("extraKeys", {
      // tab
      Tab(cm) {
        let spaces = Array(cm.getOption("indentUnit") + 1).join(" ")
        cm.replaceSelection(spaces)
      },
      'Ctrl-S' () {
        app.saveToLocal()
      },
      'Cmd-S' () {
        app.saveToLocal()
      }
    })
    // 
    this.editor.on('update', () => {
      this.htmlContent = marked(this.editor.getValue())
    })
  }
}
</script>
<style lang="stylus">
@import '~client/assets/base.styl'
@import '~codemirror/lib/codemirror.css'
.md-setting
  height: 40px
  padding: 0 $ruler-big
  box-shadow: 0 0px 4px rgba(0,0,0,0.157), 0 0px 4px rgba(0,0,0,0.227)
  z-index: 10
.md-setting-item
  display: inline-block
  height: 30px
  margin: 5px 0 5px
  padding: 0 10px
  border-radius: $border-radius-small
  line-height: 30px
  cursor: pointer
  &:hover
    background-color: $color-bg-light
    color: $color-text-white
.md-setting-logo
  font-size: $font-size-big
  color: $color-text-cyan!important
.CodeMirror
  width: 100%
  height: 100%
.markdown-body
  width: 100%
  height: 100%
  padding-left: $ruler-large
  overflow-y: scroll
.CodeMirror-gutters
  left: 0!important
.CodeMirror-gutter-wrapper
  left: -30px!important
</style>



